import { AxiosRequestConfig } from 'axios';
import { useState } from 'react';
import {Link} from 'react-router-dom';
import { UiFileInputButton } from '../../components/uifileinput';
import Spinner from '../../components/spinner';
import fileDownload from 'js-file-download';
import classes from './PdfConverter.module.css';
import MyList from '../../components/lists/lists';
import instance, {clientUrl} from '../../utils/axios';

function PdfConverter() {
    const [imgUrls, setImgUrls] = useState<string[]>([]);
    const [isUploading, setIsUploading] = useState<boolean>(false);
    const [histories, setHistories] = useState<{ name: string; time: number }[]>([]);
    const handleAllDownloads = async () => {

        for (let url of imgUrls) {
            await handleDownload(url);
        }
    }
    const handleZipDownloads = async () => {
        let fileNames: string[] = [];
        for (let url of imgUrls) {
            fileNames.push(url.substring(url.lastIndexOf('/') + 1));
        }
        const config: AxiosRequestConfig = {
            responseType: 'blob',
            headers: {
                'Content-Type': 'application/json', 'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'POST', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },
        };
        const response = await instance.post('convert/zip', { fileNames }, config);
        const blob = new Blob([response.data]);
        const zipName = fileNames[0].split('-')[0];
        fileDownload(blob, zipName.split('.')[0] + '.zip');
    }
    const handleDownload = async (url: string) => {
        const config: AxiosRequestConfig = {
            responseType: 'blob',
            headers: {
                'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'GET', 'Access-Control-Allow-Headers': 'Content-Type, Authorization',
            },
        };
        const fileName = url.substring(url.lastIndexOf('/'));

        const response = await instance.get('files/images' + fileName, config);
        const blob = new Blob([response.data], { type: 'image/jpg' });
        fileDownload(blob, fileName.substring(1));
    }

    const handleButton = async (formData: any) => {
        setIsUploading(true);
        setImgUrls([]);
        setHistories([]);
        const config: AxiosRequestConfig = {
            responseType: 'json',
            headers: {
                'Content-Type': 'multipart/form-data', 'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'POST', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },
        };

        const response = await instance.post('convert/jpg', formData, config);
        setIsUploading(false);
        setImgUrls(response.data.imageUrls);
    }
    const getHistory = async () => {
        setIsUploading(true);
        setImgUrls([]);
        const config: AxiosRequestConfig = {
            responseType: 'json',
            headers: {
                'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'POST', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },

        };
        const response = await instance.get('history/pdf', config);
        setIsUploading(false);
        setHistories(response.data.files);
    }
    const downloadPDF = async (fileName: string) => {
        const config: AxiosRequestConfig = {
            responseType: 'blob',
            headers: {
                'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'GET', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },
        };
        const response = await instance.get('files/pdf/' + fileName.replace('.pdf', ''), config);
        const blob = new Blob([response.data]);
        fileDownload(blob, fileName);
    }
    const convertExisting = async (fileName: string) => {
        setIsUploading(true);
        setImgUrls([]);
        setHistories([]);
        const config: AxiosRequestConfig = {
            responseType: 'json',
            headers: {
                'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'GET', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },
        };
        const response = await instance.get('convert/jpg/' + fileName.replace('.pdf', ''), config);
        setIsUploading(false);
        setImgUrls(response.data.imageUrls);
    }
    const deletePDF = async (fileName: string) => {
        const config: AxiosRequestConfig = {
            responseType: 'json',
            headers: {
                'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'DELETE', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },
        };
        await instance.delete('files/pdf/' + fileName.replace('.pdf', ''), config);
        const response = await instance.get('history/pdf');
        setIsUploading(false);
        setHistories(response.data.files);

    }
    const dClasses = [classes.Centered, classes.Button].join(' ');
    let spinner = null;
    let downloadAllButton = null;
    let downloadZipButton = null;
    let historyButton = null;
    if (isUploading) {
        spinner = <div className={classes.Centered}><Spinner /></div>;
    } else {
        historyButton = <div className={dClasses} onClick={getHistory}>History</div>;
        if (imgUrls.length !== 0) {
            downloadAllButton = <div className={dClasses} onClick={() => handleAllDownloads()}>Download All</div>;
            downloadZipButton = <div className={dClasses} onClick={() => handleZipDownloads()}>Download As Zip</div>;
        }
    }
    const historyList = histories.length !== 0 ? <div className={classes.Centered}><MyList iconName='pdf.png' histories={histories} downloadHandler={downloadPDF} convertExisting={convertExisting} deletePDF={deletePDF} /></div> : null;


    return (
        <div>
            <div className={classes.Grouped}>
            <div className={classes.UploadButton}>
                <UiFileInputButton
                    label="Upload PDF"
                    uploadFileName="pdf"
                    onChange={handleButton}
                    acceptedFileTypes=".pdf"
                />
            </div>
            <Link to='/video'>
            <div className={classes.Switch}>Video Converter</div>
            </Link>
            </div>
            {spinner}
            <div className={classes.Centered}>
                {downloadAllButton}
                {downloadZipButton}
                {historyButton}
            </div>

            {historyList}
            <div className={classes.Centered}>
                {
                    imgUrls.map((url, i) => {
                        return (
                            <div className={classes.Responsive} key={i}>
                                <div className={classes.Gallery}>
                                    <a target="_blank" rel='noreferrer' href={url}>
                                        <img src={url} alt={url} width="600" height="400" />
                                    </a>
                                    <div className={classes.Button} onClick={() => handleDownload(url)}>Download Page {i + 1}</div>
                                </div>
                            </div>
                        );
                    })
                }
            </div>
            <div className={classes.Clearfix}></div>

        </div>
    );
}

export default PdfConverter;