import { AxiosRequestConfig } from 'axios';
import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import Select from 'react-select';
import socketIOClient from 'socket.io-client';
import { UiFileInputButton } from '../../components/uifileinput';
import Spinner from '../../components/spinner';
import fileDownload from 'js-file-download';
import classes from './Video.module.css';
import instance, { baseURL, clientUrl } from '../../utils/axios';
import MyList from '../../components/lists/lists';

type sizeType = {
    value: { width: string, height: string; };
    label: string;
};
const sizeOptions: sizeType[] = [
    { value: { width: '?', height: '240' }, label: '240p' },
    { value: { width: '?', height: '480' }, label: '480p' },
    { value: { width: '?', height: '720' }, label: '720p' },
    { value: { width: '?', height: '1080' }, label: '1080p' },
    { value: { width: '?', height: '2160' }, label: '2160p' },
    { value: { width: '?', height: '?' }, label: 'Custom Size' }
];

const validateSize = ({ value }: sizeType): boolean => {
    if (value.width === '?' && value.height === '?') return false;
    let isWidthValid = false;
    let isHeightValid = false;
    if (value.width === '?') {
        isWidthValid = true;
    } else {
        isWidthValid = !isNaN(Number(value.width));
    }
    if (value.height === '?') {
        isHeightValid = true;
    } else {
        isHeightValid = !isNaN(Number(value.height));
    }
    return isWidthValid && isHeightValid;
}

function VideoConverter() {
    useEffect(() => {
        return () => {
            console.log("Cleanup");
        }
    }, []);
    const [isUploading, setIsUploading] = useState<boolean>(false);
    const [isConverting, setIsConverting] = useState<boolean>(false);
    const [uploadProgress, setUploadProgress] = useState<number>(0);
    const [error, setError] = useState<string | null>(null);
    const [keepDAR, setKeepDAR] = useState<boolean>(true);
    const [progress, setProgress] = useState<string>('0');
    const [currentSize, setCurrentSize] = useState<sizeType>({ value: { width: '?', height: '720' }, label: '720p' });
    const [histories, setHistories] = useState<{ name: string; time: number }[]>([]);

    const handleSizeChange = (size: sizeType | null) => {
        if (!size) return;
        setCurrentSize(size);
    }

    const handleCustomSize = (width: string, height: string) => {
        const { label } = currentSize;
        setCurrentSize({ value: { width, height }, label });
    }

    const handleButton = async (formData: FormData) => {

        if (!validateSize(currentSize)) {
            setError("Invalid Size");
            return;
        } else {
            setError(null);
        }

        formData.append('size', currentSize.value.width + 'x' + currentSize.value.height);
        formData.append('keepDAR', `${keepDAR}`);
        setIsUploading(true);
        setUploadProgress(0);
        setProgress('0');
        const config: AxiosRequestConfig = {
            responseType: 'blob',
            headers: {
                'Content-Type': 'multipart/form-data', 'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'POST', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },
            onUploadProgress: (e) => {
                const _progress = e.loaded * 100 / e.total;
                if (_progress < 100) {
                    setUploadProgress(_progress);
                } else {
                    setIsUploading(false);
                    setIsConverting(true);
                }

            }
        };
        // const fileName = (formData.get('video')! as any).name as string;
        const socket = socketIOClient(baseURL, {
            withCredentials: true,
        }
        );

        socket.on('connect', async () => {
            socket.on('PROGRESS', data => {
                setProgress(data.toFixed(2));
            });
            console.log(socket.id);
            formData.append('socketId', socket.id);
            const response = await instance.post('videoconvert/resize', formData, config);
            socket.disconnect();
            setIsConverting(false);
            const blob = new Blob([response.data]);
            fileDownload(blob, (formData.get('video')! as any).name);
        });

        socket.on("disconnect", () => {
            console.log("disconnected");
        })

    }
    const convertExisting = async (fileName: string) => {

        if (!validateSize(currentSize)) {
            setError("Invalid Size");
            return;
        } else {
            setError(null);
        }
        const body = {
            size: currentSize.value.width + 'x' + currentSize.value.height,
            keepDAR: `${keepDAR}`,
            fileName: fileName
        };
        setIsUploading(true);
        setUploadProgress(0);
        setHistories([]);
        const config: AxiosRequestConfig = {
            responseType: 'blob',
            headers: {
                'Content-Type': 'application/json', 'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'POST', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },
            onUploadProgress: (e) => {
                const progress = e.loaded * 100 / e.total;
                if (progress < 100) {
                    setUploadProgress(progress);
                } else {
                    setIsUploading(false);
                    setIsConverting(true);
                }

            }
        };

        const response = await instance.post('videoconvert/resizeExisting', body, config);
        setIsConverting(false);
        const blob = new Blob([response.data]);
        fileDownload(blob, fileName);
    }
    const getHistory = async () => {
        setIsUploading(true);
        const config: AxiosRequestConfig = {
            responseType: 'json',
            headers: {
                'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'POST', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },

        };
        const response = await instance.get('history/video', config);
        setIsUploading(false);
        setHistories(response.data.files);
    }
    const downloadVideo = async (fileName: string) => {
        const config: AxiosRequestConfig = {
            responseType: 'blob',
            headers: {
                'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'GET', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },
        };
        const response = await instance.get('files/video/' + fileName, config);
        const blob = new Blob([response.data]);
        fileDownload(blob, fileName);
    }
    const deleteVideo = async (fileName: string) => {
        const config: AxiosRequestConfig = {
            responseType: 'json',
            headers: {
                'Access-Control-Allow-Origin': clientUrl, 'Access-Control-Allow-Methods': 'DELETE', 'Access-Control-Allow-Headers': 'Content-Type, Authorization'
            },
        };
        await instance.delete('files/video/' + fileName, config);
        const response = await instance.get('history/video');
        setIsUploading(false);
        setHistories(response.data.files);

    }
    const dClasses = [classes.Centered, classes.Button].join(' ');
    let spinner = null;
    let progressMessage = null;
    let downloadAllButton = null;
    let downloadZipButton = null;
    let historyButton = null;
    if (isUploading) {
        spinner = <div className={classes.Centered}><Spinner /></div>;
        progressMessage = <div className={classes.Centered}>{uploadProgress}% Uploaded</div>;
    } else if (isConverting) {
        spinner = <div className={classes.Centered}><Spinner /></div>;
        progressMessage = <div className={classes.Centered}> {progress}% Converted</div>;
    }
    else {
        historyButton = <div className={dClasses} onClick={getHistory}>History</div>;
    }
    let customSizeInputs = null;
    if (currentSize.label === 'Custom Size') {
        customSizeInputs = (
            <div className={classes.Centered}>
                <input className={classes.Inputs} onChange={(e) => handleCustomSize(e.target.value, currentSize.value.height)} type="text" id="width" name="width" />
                X
                <input className={classes.Inputs} onChange={(e) => handleCustomSize(currentSize.value.width, e.target.value)} type="text" id="height" name="height" /></div>
        );

    }

    let errorMessage = null;
    if (error) {
        errorMessage = <div className={classes.Centered}>{error}</div>;
    }
    const historyList = histories.length !== 0 ? <div className={classes.Centered}><MyList iconName='video.png' histories={histories} downloadHandler={downloadVideo} convertExisting={convertExisting} deletePDF={deleteVideo} /></div> : null;

    return (
        <div>
            <div className={classes.Grouped}>
                <div className={classes.UploadButton}>
                    <UiFileInputButton
                        label="Upload Video"
                        uploadFileName="video"
                        onChange={handleButton}
                        acceptedFileTypes="video/*"
                    />
                </div>
                <Link to='/'>
                    <div className={classes.Switch}>PDF Converter</div>
                </Link>
            </div>
            {errorMessage}
            <div className={classes.Centered}>
                <label>
                    <input type="checkbox" checked={keepDAR} onChange={(e) => setKeepDAR(e.target.checked)} />
                    Keep Aspect Ratio
                </label>
            </div>
            <div className={classes.SelectContainer}>
                <Select className={classes.Select} options={sizeOptions} defaultValue={currentSize} isSearchable={false} onChange={handleSizeChange} />

            </div>

            {customSizeInputs}
            {spinner}
            {progressMessage}
            <div className={classes.Centered}>
                {downloadAllButton}
                {downloadZipButton}
                {historyButton}
            </div>
            {historyList}

            <div className={classes.Clearfix}></div>

        </div>
    );
}
export default VideoConverter;