import classes from './l.module.css';
import {baseURL} from '../../utils/axios';
interface propType {
    histories: {
        name: string;
        time: number;
    }[];
    downloadHandler: (name: string) => void;
    convertExisting: (name: string) => void;
    deletePDF: (name: string) => void;
    iconName: string;
}

function MyList(props: propType) {
    const ulClasses = [classes.W3ul, classes.W3Card4].join(' ');
    const imgClasses = [classes.BarItem, classes.Circle, classes.HideSmall, classes.Img].join(' ');
    return (
        <ul className={ulClasses}>
            {props.histories.map(h => {
                const humanReadableDate = new Date(h.time).toLocaleDateString('en-US', {
                    day: 'numeric',
                    month: 'numeric',
                    year: 'numeric',
                    hour: '2-digit',
                    minute: '2-digit'
                });
                return (<li className={classes.Bar} key={h.name}>
                <img src={baseURL + "/assets/" + props.iconName} alt="pdf icon" className={imgClasses} onClick={() => props.downloadHandler(h.name)} />
                    <div className={classes.BarItem} onClick={()=>props.convertExisting(h.name)}>
                        <span className={classes.Large}>{h.name}</span><br />
                        <span>{humanReadableDate}</span>
                    </div>
                    <span className={classes.Trash} onClick={() => props.deletePDF(h.name)}></span>
                </li>);
            })}

        </ul>
    );
}
export default MyList;